* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  background-color: orange;
}
.container {
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 100px;
  display: flex;
  flex-direction: column;
}
#search-box {
  align-self: center;
  width: 30%;
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  background-color: whitesmoke;
  border-radius: 8px;
}

.list {
  list-style: none;
}
.language:not(:last-child):after {
  content: ', ';
}
.country__card {
  color: whitesmoke;
}
.country-info-item {
  font-size: 25px;
  font-weight: 500;
}
.country-name {
  font-size: 35px;
  font-weight: 700;
}
.country-list-name {
  font-size: 25px;
  font-weight: 500;
  color: whitesmoke;
  margin-left: 20px;
}
